:root {
  --ferrari: rgb(220,0,0);
  --mercedes: rgb(0,210,190);
  --redbull: rgb(30,65,255);
  --renault: rgb(255,245,0);
  --haas: rgb(240,215,135);
  --racingpoint: rgb(245,150,200);
  --tororosso: rgb(70,155,255);
  --mclaren: rgb(255,135,0);
  --alfaromeo: rgb(155,0,0);
  --williams: rgb(255,255,255);
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button, select {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

select {
  font-size: 16px;
  padding: 4px;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  border: 1px solid #ddd;
  width: 100%;
}

th, td {
  text-align: left;
  padding: 10px;
}

th, tr:nth-child(even) {
  background-color: #D3D3D3;
}

/* Ferrari */
.bg-ferrari, .h-ferrari:hover {
  background-color: rgb(220,0,0);
  background-color: var(--ferrari);
}

.b-ferrari {
  border: 1px solid rgb(220,0,0);
  border: 1px solid var(--ferrari);
}

/* Mercedes */
.bg-mercedes, .h-mercedes:hover {
  background-color: rgb(0,210,190);
  background-color: var(--mercedes);
}

.b-mercedes {
  border: 1px solid rgb(0,210,190);
  border: 1px solid var(--mercedes);
}

/* Red Bull */
.bg-redbull, .h-redbull:hover {
  background-color: rgb(30,65,255);
  background-color: var(--redbull);
}

.b-redbull {
  border: 1px solid rgb(30,65,255);
  border: 1px solid var(--redbull);
}

/* Renault */
.bg-renault, .h-renault:hover {
  background-color: rgb(255,245,0);
  background-color: var(--renault);
}

.b-renault {
  border: 1px solid rgb(255,245,0);
  border: 1px solid var(--renault);
}

/* Haas */
.bg-haas, .h-haas:hover {
  background-color: rgb(240,215,135);
  background-color: var(--haas);
}

.b-haas {
  border: 1px solid rgb(240,215,135);
  border: 1px solid var(--haas);
}

/* Racing Point */
.bg-racingpoint, .h-racingpoint:hover {
  background-color: rgb(245,150,200);
  background-color: var(--racingpoint);
}

.b-racingpoint {
  border: 1px solid rgb(245,150,200);
  border: 1px solid var(--racingpoint);
}

/* Toro Rosso */
.bg-tororosso, .h-tororosso:hover {
  background-color: rgb(70,155,255);
  background-color: var(--tororosso);
}

.b-tororosso {
  border: 1px solid rgb(70,155,255);
  border: 1px solid var(--tororosso);
}

/* McLaren */
.bg-mclaren, .h-mclaren:hover {
  background-color: rgb(255,135,0);
  background-color: var(--mclaren);
}

.b-mclaren {
  border: 1px solid rgb(255,135,0);
  border: 1px solid var(--mclaren);
}

/* Alfa Romeo */
.bg-alfaromeo, .h-alfaromeo:hover {
  background-color: rgb(155,0,0);
  background-color: var(--alfaromeo);
}

.b-alfaromeo {
  border: 1px solid rgb(155,0,0);
  border: 1px solid var(--alfaromeo);
}

/* Williams */
.bg-williams {
  background-color: rgb(255,255,255);
  background-color: var(--williams);
}

.h-williams:hover, .bg-williams.active {
  background-color: rgb(204,204,204);
}

.b-williams {
  border: 1px solid black;
}

.button {
  padding: 5px 10px;
  font-size: 16px;
  font-weight: bold;
  color: white;
  transition: 0.3s;
}

.button.bg-mercedes, .button.bg-renault, .button.bg-haas,
.button.bg-racingpoint, .button.bg-tororosso, .button.bg-mclaren,
.button.bg-williams {
  color: black;
}

.button:hover {
  cursor: pointer;
  opacity: 0.5;
}

.container {
  padding-top: 10px;
  padding-bottom: 10px;
}

@media only screen and (min-width: 600px) {
  .container {
    margin-left: 40px;
    margin-right: 40px;
  }
}

@media only screen and (min-width: 768px) {
  .container {
    margin-left: 80px;
    margin-right: 80px;
  }
}

@media only screen and (min-width: 992px) {
  .container {
    margin-left: 160px;
    margin-right: 160px;
  }
}

@media only screen and (min-width: 1200px) {
  .container {
    margin-left: 320px;
    margin-right: 320px;
  }
}

.mb10 {
  margin-bottom: 10px;
}

.ml10 {
  margin-left: 10px;
}

.mr10 {
  margin-right: 10px;
}

.mt10 {
  margin-top: 10px;
}

.p10 {
  padding: 10px;
}

.responsive-table {
  overflow-x: auto;
}

.tr {
  text-align: right;
}

h1 {
  margin: 0;
}

h1.ferrari, h1.redbull, h1.alfaromeo {
  color: white;
}

option[value=ferrari], option[value=redbull], option[value=alfaromeo] {
  color: white;
}

.dib {
  display: inline-block;
}

.select-wrapper {
  margin-left: 10px;
  margin-bottom: 0;
  padding-bottom: 10px;
}

@media only screen and (min-width: 600px) {
  .flex {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }

  .select-wrapper {
    margin: 15px;
    padding-bottom: 0;
  }
}

nav {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

nav button {
  background-color: white;
  letter-spacing: 1px;
  padding: 5px;
  font-weight: bold;
  border: none;
  cursor: pointer;
}

nav button.h-ferrari:hover, nav button.bg-ferrari.active,
nav button.h-redbull:hover, nav button.bg-redbull.active,
nav button.h-alfaromeo:hover, nav button.bg-alfaromeo.active {
  color: white;
}

.race {
  padding-top: 5px;
  padding-bottom: 5px;
}

.race:hover {
  cursor: pointer;
  background-color: #A9A9A9;
}

.unselectable {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.striped {
  background-color: #D3D3D3;
}

.upcoming {
  background-color: rgba(220,0,0,0.5);
}

.round {
  display: inline-block;
  width: 10%;
  text-align: right;
  padding-right: 10px;
}

.location {
  display: inline-block;
  width: 45%;
}

.date-time {
  display: inline-block;
  width: 45%;
  padding-right: 10px;
}

.date {
  display: inline-block;
  width: 50%;
  text-align: right;
  padding-right: 10px;
}

.time {
  display: inline-block;
  width: 50%;
  text-align: right;
}

@media only screen and (max-width: 600px) {
  .round {
    display: none;
  }

  .location {
    width: 60%;
    padding-left: 10px;
  }

  .date-time {
    width: 40%;
  }

  .date {
    width: 100%;
  }

  .time {
    display: none;
  }
}

.break-word {
  word-break: break-all;
}

.button-left, .button-right {
  float: none;
}

@media only screen and (min-width: 600px) {
  .button-left {
    float: left;
  }

  .button-right {
    float: right;
  }
}

.prev-next {
  overflow: auto;
}

.fastest {
  color: crimson;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

.season {
  display: inline-block;
}

@media only screen and (min-width: 600px) {
  .season {
    margin-left: 0;
  }
}

